body {
  background-color: #da8888;
  box-sizing: border-box;
  margin: 0;
  padding: 0%;
  font-family: "Josefin Sans",sans-serif;
}

div {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -30px;
}

h1 {
  padding: 20px 20px;
  background-color: peachpuff;
  color: rgb(100, 74, 215);
  border-radius: 20px;
}

span {
  color: #fff;
}